import styled from "styled-components";
import { palette } from "styled-theme";
// import { transition, borderRadius } from "../../settings/style-util";
// import WithDirection from "../../settings/withDirection";
import { Link } from "react-router-dom";

const DetailWrapper = styled.div`
  font-family: ${({ theme }) => theme.font};
  font-weight: 400;
  display: flex;
  flex-direction: column;
  .top-sec {
    // display: flex;
    // align-content: center;
    // align-items: center;
    // border: 1px solid red;
    // border-radius: 24px;
    padding: 10px 15px;
    background: ${({ theme }) => theme.colors.primary};
    .cal-cont {
      display: flex;
      flex-direction: column;
      align-content: center;
      align-items: center;
      justify-content: space-between;
      color: ${({ theme }) => theme.colors.white};

      width: 100%;
      .btc-tip {
        display: flex;
        flex-direction: row;
        align-content: center;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 10px;
        div {
          height: 100%;
          margin-left: 10px;
          margin-top: 0px;
          &:nth-child(1) {
            margin-left: 0px;
          }
          &:nth-last-child(1) {
            margin-right: 0px;
          }
        }
        .a {
          flex: 1;
        }
      }
      .btc-total {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-content: flex-start;
        align-items: flex-start;
      }
      .box {
        background: ${({ theme }) => theme.colors.secondary};
        width: 100%;
        padding: 15px;
        border-radius: 16px;
        box-sizing: border-box;
        .label {
          // font-size: 24px;
          color: ${({ theme }) => theme.colors.primary};
        }
        .btc-tip-amount {
          font-size: 24px;
          margin: 0px;
        }
        .total-amount {
          font-size: 48px;
          margin: 0px;
        }
      }
    }
  }
  .bottom-sec {
    background: ${({ theme }) => theme.colors.primary};
    .bottom-sec-inner {
      background: white;
      display: flex;
      flex-direction: column;
      border-top-left-radius: 24px;
      padding: 25px;
      .user-info {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 15px;
        padding-bottom: 15px;
        border-bottom: 1px solid ${({ theme }) => theme.colors.grayLight};
        .user-img {
          width: 60px;
          height: 60px;
          border-radius: 16px;
          overflow: hidden;
          img {
            width: 100%;
          }
        }
        .user-detail {
          display: flex;
          flex-direction: row;
          justify-content: flex-start;
          .user-name {
            margin-left: 8px;
          }
        }
        .status {
          div {
            font-size: 28px;
          }
        }
      }
      .tr-info {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-top: 10px;
        margin-bottom: 15px;

        .left {
          div {
            display: inline-block;
            margin-right: 10px;
          }
        }
        // .user-img {
        //   width: 60px;
        //   height: 60px;
        //   border-radius: 16px;
        //   overflow: hidden;
        //   img {
        //     width: 100%;
        //   }
        // }
        // .user-detail {
        //   display: flex;
        //   flex-direction: row;
        //   justify-content: flex-start;
        //   .user-name {
        //     margin-left: 8px;
        //   }
        // }
      }
    }
  }
`;

export default DetailWrapper;

export const Icon = styled.div`
  // background: red;
`;
