import React from "react";

//STYLE
import {
  BodyH1,
  BodyH2,
  BodyH3,
  BodyH4,
  BodyH5,
} from "../../../theme/GlobalStyles";
import DetailWrapper, { Icon } from "./transactiondetail.style";
import { FiAlignLeft, FiGrid, FiRepeat, FiMinus, FiPlus } from "react-icons/fi";
import {
  HiOutlineQrcode,
  HiOutlineMinusCircle,
  HiOutlinePlusCircle,
  HiOutlineClock,
  HiOutlineCheckCircle,
  HiOutlineXCircle,
  HiOutlineEye,
  HiOutlinePencilAlt,
  HiOutlineTrash,
} from "react-icons/hi";
import { useTheme } from "../../../theme/useTheme";

const TransactionDeatil = (props) => {
  const { theme, themeLoaded } = useTheme();
  console.log("theme", theme);

  const data = (props.location && props.location.state) || {};
  const [locData, setlocData] = React.useState(data);
  React.useEffect(() => {
    setlocData(data);
  }, [locData]);
  console.log("state", props);
  const renderStatus = (status, iconSize, iconColor) => {
    if (status == "All") {
      return null;
    }
    if (status == "Pending") {
      return <HiOutlineClock style={{ color: theme.colors.yellow }} />;
    }
    if (status == "Complete") {
      return <HiOutlineCheckCircle style={{ color: theme.colors.green }} />;
    }
    if (status == "Cancelled") {
      return <HiOutlineXCircle style={{ color: theme.colors.red }} />;
    }
    if (status == "Overdue") {
      return <HiOutlineXCircle style={{ color: theme.colors.gray }} />;
    }
  };
  return (
    <DetailWrapper>
      <div className="top-sec">
        <div className="cal-cont">
          <div className="btc-tip">
            <div className="a">
              <div className="box">
                <div className="label">BTC</div>
                <div className="btc-tip-amount">0.01539</div>
              </div>
            </div>
            <div className="">
              <div className="box">+</div>
            </div>
            <div className="a">
              <div className="box">
                <div className="label">Tip</div>
                <div className="btc-tip-amount">0.01539</div>
              </div>
            </div>
          </div>
          <div className="btc-total">
            <div className="box">
              <div className="label">Total</div>
              <div className="total-amount">0.01539</div>
            </div>
          </div>
        </div>
      </div>
      <div className="bottom-sec">
        <div className="bottom-sec-inner">
          <div className="user-info">
            <div className="user-detail">
              <div className="user-img">
                <img src={data.picture.medium} alt="" />
              </div>
              <div className="user-name">
                <BodyH1>
                  {data.name.first}
                  {data.name.last}
                </BodyH1>
                <BodyH3>Memo</BodyH3>
              </div>
            </div>
            <div className="status">
              <Icon>{renderStatus(data.status)}</Icon>
            </div>
          </div>

          <div className="tr-info">
            <div className="left">
              <Icon>
                <FiRepeat />
              </Icon>
              <BodyH3>{data.dob}</BodyH3>
            </div>
            <div>
              <Icon>
                <FiRepeat />
              </Icon>
            </div>
          </div>
          <div className="tr-info">
            <div className="left">
              <Icon>
                <FiRepeat />
              </Icon>
              <BodyH3>{data.dob}</BodyH3>
            </div>
            <div>
              <Icon>{/* <FiRepeat /> */}</Icon>
            </div>
          </div>
          <div className="tr-info">
            <div className="left">
              <Icon>
                <FiRepeat />
              </Icon>
              <BodyH3>{data.email}</BodyH3>
            </div>
            <div>
              <Icon>{/* <FiRepeat /> */}</Icon>
            </div>
          </div>
          <div className="tr-info">
            <div className="left">
              <Icon>
                <FiRepeat />
              </Icon>
              <BodyH3>{data.gender}</BodyH3>
            </div>
            <div>
              <Icon>{/* <FiRepeat /> */}</Icon>
            </div>
          </div>
          <div className="tr-info">
            <div className="left">
              <Icon>
                <FiRepeat />
              </Icon>
              <BodyH3>{data.email}</BodyH3>
            </div>
            <div>
              <Icon>
                <FiRepeat />
              </Icon>
            </div>
          </div>
          <div className="tr-info">
            <div className="left">
              <Icon>
                <FiRepeat />
              </Icon>
              <BodyH3>{data.email}</BodyH3>
            </div>
            <div>
              <Icon>
                <FiRepeat />
              </Icon>
            </div>
          </div>
          <div className="tr-info">
            <div className="left">
              <Icon>
                <FiRepeat />
              </Icon>
              <div>
                <BodyH3>{data.registered}</BodyH3>
              </div>
            </div>
            <div>
              <Icon>
                <FiRepeat />
              </Icon>
            </div>
          </div>
        </div>
      </div>
    </DetailWrapper>
  );
};
export default TransactionDeatil;
